import * as L from './_snowpack/pkg/leaflet.js';
import { ApiService } from './services/api-service.js';
import { CurrentLocationService } from './services/current-location-service.js';
import { CurrentViewCenterService } from './services/current-view-center-service.js';

window.L = L;

const initPos = {
  lat: 47.8555,
  lon: 35.2916,
};

// initialize Leaflet
const map = window.map = L.map('map').setView(initPos, 3.5, {

});

// add the OpenStreetMap tiles
L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
  maxZoom: 19,
  attribution: '&copy; <a href="https://openstreetmap.org/copyright">OpenStreetMap contributors</a>',

}).addTo(map);

// show the scale bar on the lower left corner
L.control.scale({ imperial: false, metric: true }).addTo(map);

const api = new ApiService();
// const locationService = new CurrentLocationService();
const locationService = new CurrentViewCenterService(map);

// show a marker on the map
async function onMapClick (e) {
  // alert("You clicked the map at " + e.latlng);
  console.log(e);

  api.getCellId(e.latlng)
    .then(bounds => {
      const [a,b,c,d] = bounds;
      L.rectangle([[a,b], [c,d]]).addTo(map);
    });


  const points = await api.getPoints(e.latlng);
  let i = 0;
  for (const point of points) {
    L.circle(point, {
      color: 'blue',
      fillColor: '#f03',
      fillOpacity: 0.5,
      radius: 30,
    }).addTo(map);
    // L.marker(point).bindPopup(`lnglat: ${point.lng} ${point.lat}`).addTo(map);
    if (i++ > 100) break;
  }
  console.log(points);
}

map.on('click', onMapClick);

const markers = [];

async function hit () {

  const pos = await locationService.get();

  map.setView(pos, 15, { animate: true });
  setPlayerMarker(pos);

  const resp = await api.hitAt(pos);

  for (const marker of markers) marker.removeFrom(map);
  markers.splice(0, markers.length);


  for (const point of resp.points) {
    const marker = L.circle(point, {
      color: 'blue',
      fillColor: '#f03',
      fillOpacity: 0.5,
      radius: 30,
    }).addTo(map);

    markers.push(marker);
  }

  setTimeout(hit, 4000);
}

hit();



function setPlayerMarker (pos) {
  if (!setPlayerMarker._m) {
    setPlayerMarker._m = L.marker(pos).bindPopup(`Ti tyt`).addTo(map);
  }

  setPlayerMarker._m.setLatLng(pos).update();

  return setPlayerMarker._m;
}
