import axios from '../_snowpack/pkg/axios.js';
import { API_HOST } from '../constants.js';


export class ApiService {


  async getPoints ({ lat, lng }) {
    const resp = await axios.get(`${API_HOST}/points`, {
      params: {
        lat,
        lng,
      },
    });

    return resp.data;
  }


  async getCellId ({ lat, lng }) {
    const resp = await axios.get(`${API_HOST}/cell-id`, {
      params: {
        lat,
        lng,
      },
    });

    return resp.data;
  }

  async hitAt ({ lat, lng }) {
    const resp = await axios.post(`${API_HOST}/user-pos`, null,  {
      params: { lat, lng },
    });

    return resp.data;
  }

}
